import React from 'react';
import Card from './Card';


const Cardlist = ({robotsid}) => {

	const cardArray = robotsid.map((user,i) => {

		return(

		<Card key={i} id={robotsid[i].id} name={robotsid[i].name} email={robotsid[i].email}/>

			);


	})
return(
	<div> 
			{cardArray}
   </div>
		  
);

}

export default Cardlist;